@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&family=Roboto:ital,wght@0,100;0,400;0,700;1,300&display=swap);
.single-card {
  background: white;
  width: 100%;
  border-radius: 4px;
  margin-bottom: 8px;
  box-shadow: 3px 3px 5px #00000011;
}

.single-card .card-header,
.single-card .card-summary {
  padding: 16px;
  background: var(--gray);
  border-radius: 4px 4px 0 0;
  border-bottom: 1px solid #cccccc55;
  display: -webkit-flex;
  display: flex;

}

.single-card .card-header .card-header-input,
.single-card .card-summary .card-result {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.single-card .card-header .card-header-input {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.single-card .card-header .card-header-input label {
  opacity: .8;
}

.single-card .card-header .card-header-input.big {
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}


.single-card .card-header .card-header-input label {
  font-size: .8em;
}

.single-card .card-header .card-header-input input {
  outline: none;
  border: none;
  background: none;
}

.single-card .card-header .card-header-input.big input {
  color: var(--pink);
  font-weight: bold;
  font-size: 2em;
}

.single-card .card-header .card-header-input.bubble {
  margin: 6px 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.single-card .card-header .card-header-input.bubble .input-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.single-card .card-header .card-header-input.bubble input:not([type="range"]) {
  background: linear-gradient(50deg, var(--pink) 50%, var(--violet));
  color: white;
  height: 40px;
  width: auto;
  border-radius: 20px;
  padding: 8;
  font-size: 1.2em;
  padding: 8px;
  font-weight: bold;
  text-align: center;
}

.single-card .card-header .card-header-input.bubble input[type="range"] {
  margin-top: 5px;
}

.single-card .card-summary {
  background: white;
}

.single-card .card-summary .card-result {
  margin: 0 20px;
  -webkit-align-items: center;
          align-items: center;
}

.single-card .card-summary .card-result label {
  opacity: .8;
  margin-right: 15px;
}

.single-card .card-summary .card-result span {
  font-weight: light;
  color: var(--violet);
  font-size: 1.2em;
}

.single-card .card-footer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 8px;
}

.single-card .card-footer .card-remove {
  margin-left: auto;
  color: var(--fucsia);
}

.single-card .card-footer .card-clone {
  color: var(--pink);
}


.add-card,
.view-report {
  background: none;
  width: 400px;
  max-width: 90vw;
  margin: 0 auto;
  padding: 9px;
  border: 2px solid var(--pink);
  color: var(--pink);
  font-size: 2em;
  border-radius: 2em;
  cursor: pointer;
  transition: .3s all;
}

.add-card:hover,
.view-report:hover {
  background: var(--pink);
  color: var(--background);
}

@media(max-width:1300px) {
  .single-card .card-header {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .single-card .card-header .card-header-input.big {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }

  .single-card .card-header .card-header-input.bubble {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .single-card .card-header .card-header-input.big label,
  .single-card .card-header .card-header-input.big input {
    width: 350px;
  }

  .single-card .card-header .card-header-input.big input {
    border-bottom: 1px solid var(--pink);
    margin-bottom: 1em;
  }
}

@media(max-width: 768px) {
  .single-card .card-summary {
    font-size: 14px;
  }

  .single-card .card-summary .card-result label {
    margin-right: 0;
  }

  .single-card .card-summary .card-result {
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
  }

}

@media(max-width:600px) {

  .single-card .card-summary {
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: 16px;
  }

  .single-card .card-summary .card-result {
    margin: 8px 0;
  }
}

.twin-results {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.twin-results>*:first-child {
	border-right: 1px solid #ccc;
}

.big-result {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-top: 50px;
}

.big-result .single-result {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	color: var(--violet);
	font-weight: bold;
	text-align: center;
}


.big-result .result-main span {
	font-size: 5vw;
	font-family: 'Oswald';
	line-height: 1;
}

.big-result .result-secondary {
	margin-top: 50px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
}

.big-result .result-secondary .single-result {
	margin: 15px 100px;
}

.big-result .result-secondary .single-result span {
	font-size: 1.5em;
}

.result-chart {
	margin-top: 50px;
}


@media(max-width:768px) {
	.big-result .result-secondary .single-result {
		margin: 15px;
	}

	.big-result .result-secondary .single-result span {
		font-size: 1.5em;
	}

	.twin-results {
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}

	.twin-results>*:first-child {
		border-right: none;
		border-bottom: 1px solid #ccc;
	}
}

.view-report {
	color: var(--purple);
	border-color: var(--purple);
	margin-top: 40px;
	padding: 4px;
	width: 250px;
	font-size: 1.5em;
}

.view-report:hover {
	background: var(--purple);
}
.report {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, .7);
	display: -webkit-flex;
	display: flex;
}

.report .report-inner {
	margin: 20px auto;
	background: white;
	width: 800px;
	max-width: 100vw;
	overflow: auto;
	border-radius: 4px;
	padding: 16px;
}

.close-report,
.print-report {
	display: block;
	width: 100%;
	text-align: right;
	color: var(--fucsia);
	cursor: pointer;
}

.print-report {
	text-align: left;
}

.report h4,
.report h3,
.report p {
	margin-bottom: 1em;
}

.report h3 {
	text-align: center;
}

.debt-summary,
.payment-schedule {
	width: 100%;
	overflow: auto;
	margin-bottom: 2em;
}

.report table {
	width: 100%;
	border-collapse: collapse;
}

.report thead,
.report tfoot {
	color: white;
	background: var(--purple);
}

.report td,
.report th {
	padding: 8px 16px;
	border-right: 1px solid #ccc;
	text-align: right;
}

.debt-summary thead tr:first-child th {
	font-size: 1.3em;
	padding: 16px 4px;
	text-align: center;
}

.report tbody tr:nth-child(even) {
	background: #eee;
}

.report tbody tr:hover {
	background: var(--lightpink);
}

.report tbody td:hover {
	background: var(--pink);
}
:root {
	--background: #fbf6ef;
	--brown: #cfc7b8;
	--lightpink: #cdbae8;
	--purple: #341455;
	--fucsia: #b40c66;
	--gray: #fafafa;
	--violet: #33208e;
	--pink: #c24ba3;
}

body {
	padding: 0;
	margin: 0;
	background: #fbf6ef;
	background: var(--background);
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: 'Roboto', 'Helvetica', sans-serif;
}

input {
	font-family: 'Oswald'
}

.pointer {
	cursor: pointer;
}

.App {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.Header {
	margin: 50px auto 0 auto;
	width: 80vw;
	font-size: 2em;
	font-weight: bold;
	text-align: center;
}

.calculator {
	margin: 20px auto 0 auto;
	width: 80vw;
	padding: 15px;
}

.cards-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.BalanceChart {
	width: 90%;
	height: 300px;
}



@media(max-width: 940px) {

	.Header,
	.calculator {
		width: 95vw;
	}

	.BalanceChart {
		height: 250px;
	}
}

@media(max-width: 768px) {
	.BalanceChart {
		height: 200px;
	}
}

@media(max-width: 450px) {

	.Header,
	.calculator {
		width: 98vw;
	}
}

@media print{
	.close-report, .print-report{
		display:none !important;
	}
	table{
		width:100%;
	}
	.report-inner{
		padding:8px;
		text-align:center;
	}
}
